.product {
  &__photo {
    width: 100%;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 25px;
    color: $main-text-color;
  }

  &__description {
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.15px;
    color: $main-text-color;
  }

  &__price {
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    color: $main-text-color;
  }
}
