.footer {
  &__wrapper {
    @include onDesktop {
      @include pageGrid;
    }
  }

  &__content {
    margin-top: 48px;
    display: grid;
    row-gap: 24px;

    @include onTablet {
      margin-top: 80px;
    }

    @include onDesktop {
      margin-top: 207px;
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.15px;
    color: #9393a3;
    margin: 0;
    margin-bottom: 8px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.15px;
    color: #191919;
    margin: 0;
  }

  &__field {
    grid-column: 8 / 12;
  }
}
