.contact-us {
  @include onDesktop {
    grid-column: 1 / 7;
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    margin: 0;
    margin-bottom: 48px;
    margin-top: 64px;

    @include onTablet {
      margin-bottom: 56px;
      font-size: 40px;
      margin-top: 120px;
    }

    @include onDesktop {
      font-size: 48px;
    }
  }
}

.form {
  display: grid;
  row-gap: 16px;

  &__input {
    background-color: #f9f9f9;
    border-radius: 8px;
    border: none;
    height: 48px;
    padding-inline: 16px;

    &::placeholder {
      @include placeholderApp;
    }
  }

  &__message {
    border-radius: 8px;
    background-color: #f9f9f9;
    border: none;
    padding: 14px 16px;

    &::placeholder {
      @include placeholderApp;
    }
  }

  &__button {
    border-radius: 8px;
    background-color: #f98921;
    height: 56px;
    border: none;
    padding: 0;
    grid-column: 1 / -1;
    cursor: pointer;
    transition-duration: 0.3s;

    &:hover {
      background-color: #1b2129;
    }

    &:active {
      background-color: #010810;
    }

    &-title {
      text-align: center;
      vertical-align: baseline;
      font-size: 16px;
      font-weight: 700;
      line-height: 27px;
      color: #fff;
    }
  }

  &__effects {
    box-sizing: border-box;

    &:hover {
      border: 1px solid #dbdbdb;
    }

    &:active {
      border: 1px solid #f98921;
    }
  }
}
