.categories {
  &__wrapper {
    @include rowGap;

    &-button {
      @include pageGrid;
    }
  }

  &__button {
    border-radius: 8px;
    background-color: #f98921;
    height: 56px;
    border: none;
    padding: 0;
    grid-column: 1 / -1;
    cursor: pointer;
    transition-duration: 0.3s;

    @include onDesktop {
      grid-column: 1 / 5;
    }

    &-title {
      text-align: center;
      vertical-align: baseline;
      font-size: 16px;
      font-weight: 700;
      line-height: 27px;
      color: #fff;
    }

    &:hover {
      background-color: #1b2129;
    }

    &:active {
      background-color: #010810;
    }
  }
}

.category {
  &__title {
    margin: 0;
    font-size: 18px;
    font-weight: 700;
    line-height: 25px;

    @include onTablet {
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__photo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
    }
  }

  &__photos {
    @include pageGrid;

    margin-bottom: 16px;
  }

  &__link {
    height: 130px;

    @include onTablet {
      height: 184px;
    }

    @include onDesktop {
      height: 324px;
    }

    &--left {
      @include onTablet {
        grid-column: span 3;
      }

      @include onDesktop {
        grid-column: span 6;
      }
    }

    &--right {
      @include onTablet {
        grid-column: span 3;
      }

      @include onDesktop {
        grid-column: span 6;
      }
    }
  }
}
