.header {
  //display: none !important;

  background-image: url(\images\image-bg.png);
  background-size: cover;
  background-position: center;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 100vh;
  }

  &__title {
    font-size: 36px;
    font-weight: 700;
    line-height: 36px;
    color: #fff;
    margin: 0 ;

    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 64px;
    }

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }

    @include onDesktop {
      font-size: 56px;
      line-height: 56px;
    }
  }
}
