.timeless {
  &__photo {
    width: 100%;
    object-fit: cover;
    height: 200px;

    @include onTablet {
      height: 400px;
    }

    @include onDesktop {
      height: 460px;
    }
  }

  &__bg {
    width: 100%;
    background-color: #fcfaef;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    color: $main-text-color;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;

    @include onTablet {
      color: $main-text-color;
      font-size: 40px;
      line-height: 40px;

      grid-column: 1 / 4;
    }

    @include onDesktop {
      color: $main-text-color;
      font-size: 48px;
      line-height: 48px;
      grid-column: 1 / 7;
    }
  }

  &__description {
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    letter-spacing: 0.15px;
    color: $main-text-color;
    margin: 0;
    margin-top: 24px;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: 8 / 13;
    }
  }

  &__content {
    @include onTablet {
      @include pageGrid;
    }

    margin-block: 48px;

    @include onTablet {
      margin-block: 120px;
    }
  }
}
