.recommended {
  &__products {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      justify-items: center;
    }

    @include onDesktop {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 24px;
      justify-items: stretch;
    }
  }
}

.product {
  &__photo {
    transition: transform 0.3s;
    
    &:hover {
      transform: scale(1.05);
    }
  }
}
