.nav {
  &__link {
    text-decoration: none;
    color: inherit;
    font-size: 22px;
    font-weight: 700;
    line-height: 22px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100%;
      position: absolute;
      height: 1px;
      background-color: $main-text-color;

      transform: scale(0);
      transition: transform $effectDuration;
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}
